.wrapper{
    width:100vw;
    height:100vh;
    /* 将背景图片设置为渐变背景 */
    background: linear-gradient(45deg,#6fc7b5 0%,#13bdce, 20%,#0094d9 40%, #5a3694 60%, #ee4d74 80%, #f58c58 100%);
    /* 将背景区域变大 四倍显示 */
    background-size: 400%;
    /* 最初默认位置显示左下角 */
    background-position:0% 100%;
    /* 设置定位 */
    position:relative;
    /* 背景过渡动画 无限展示*/
    -webkit-animation:gradient 7.5s ease-in-out infinite;
            animation:gradient 7.5s ease-in-out infinite;
}
.wrapper *{
    /* 设置定位不根据滚动条位置变化 */
    position:fixed;
    /* wrapper下的元素居中显示 */
    left:50%;
    top:50%;
    -webkit-transform: translate3d(-50%,-50%,0) rotate(0deg);
            transform: translate3d(-50%,-50%,0) rotate(0deg);
}
.logo{
    /* 字体设置 */
    color:#fff;
    font-size: 30px;
    font-weight:bolder;
    cursor: pointer;
}
.wrapper .dots{
    width: 800px;
    height: 800px;
}
/* 包含着小球得正方形旋转一周  为小球得原形运动轨迹 */
.wrapper .dots.animate .dot{
    -webkit-animation:loading-block 2.5s ease-in-out 1;
            animation:loading-block 2.5s ease-in-out 1;
}
/* 小球自身缩放变化  变小变大 */
.wrapper .dots.animate .dot:after{
    -webkit-animation:loading-dot 2.5s ease-in-out 1;
            animation:loading-dot 2.5s ease-in-out 1;
}
/* 设置正方形 为小球圆形运动轨迹  为正方形得外接圆 */
.wrapper .dots .dot{
    height:300px;
    width:300px;
}
/* 小球为正方形得伪元素 */
.wrapper .dots .dot:after{
    content:"";
    display:inline-block;
    width:60px;
    height:60px;
    background-color:#fff;
    border-radius:50%;
    position:absolute;
    -webkit-transform: scale(0.17);
            transform: scale(0.17);
}
/* 分别设置每一个小球得位置  以及动画延迟 */
.wrapper .dots .dot:nth-child(1) {
    top: 531px;
    left:203px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}
.wrapper .dots .dot:nth-child(1):after {
    -webkit-transform-origin: top right;
            transform-origin: top right;
    top: 0;
    right: 0;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}
.wrapper .dots .dot:nth-child(2) {
    top:261px;
    left:234px;
    -webkit-animation-delay:0.25s;
            animation-delay:0.25s;
}
.wrapper .dots .dot:nth-child(2):after{
    bottom:0;
    right:0;
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
    -webkit-animation-delay:0.25s;
            animation-delay:0.25s;
}
.wrapper .dots .dot:nth-child(3) {
    top:244px;
    left:268px;
    -webkit-animation-delay:0.5s;
            animation-delay:0.5s;
}
.wrapper .dots .dot:nth-child(3):after{
    bottom:0;
    right:0;
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
    -webkit-animation-delay:0.5s;
            animation-delay:0.5s;
}
.wrapper .dots .dot:nth-child(4) {
    top:549px;
    left:592px;
    -webkit-animation-delay:0.75s;
            animation-delay:0.75s;
}
.wrapper .dots .dot:nth-child(4):after{
    top:0;
    left:0;
    -webkit-transform-origin: top left;
            transform-origin: top left;
    -webkit-animation-delay:0.75s;
            animation-delay:0.75s;
}
.wrapper .dots .dot:nth-child(5) {
    top:270px;
    left:588px;
    -webkit-animation-delay:1s;
            animation-delay:1s;
}
.wrapper .dots .dot:nth-child(5):after{
    bottom:0;
    left:0;
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
    -webkit-animation-delay:1s;
            animation-delay:1s;
}
/* 利用关键帧设置动画 */
@-webkit-keyframes loading-block {
0% {
        -webkit-transform: translate3d(-50%,-50%,0) rotate(0deg);
                transform: translate3d(-50%,-50%,0) rotate(0deg);
}
100% {
        -webkit-transform: translate3d(-50%,-50%,0) rotate(360deg);
                transform: translate3d(-50%,-50%,0) rotate(360deg);
}
}
@keyframes loading-block {
0% {
        -webkit-transform: translate3d(-50%,-50%,0) rotate(0deg);
                transform: translate3d(-50%,-50%,0) rotate(0deg);
}
100% {
        -webkit-transform: translate3d(-50%,-50%,0) rotate(360deg);
                transform: translate3d(-50%,-50%,0) rotate(360deg);
}
}
/* 小球自身大小变化 */
@-webkit-keyframes loading-dot {
0% {
        -webkit-transform:scale(0.17);
                transform:scale(0.17);
}
50% {
        -webkit-transform:scale(1);
                transform:scale(1);
}
100% {
        -webkit-transform:scale(0.17);
                transform:scale(0.17);
}
}
@keyframes loading-dot {
0% {
        -webkit-transform:scale(0.17);
                transform:scale(0.17);
}
50% {
        -webkit-transform:scale(1);
                transform:scale(1);
}
100% {
        -webkit-transform:scale(0.17);
                transform:scale(0.17);
}
}
/* 背景过渡动画  利用渐变背景得位置定位显示  逐渐将动画展示出来*/
@-webkit-keyframes gradient{
0% {
        background-position: 0% 100%;
}
50% {
        background-position: 100% 0%;
}
100% {
        background-position: 0% 100%;
}
}
@keyframes gradient{
0% {
        background-position: 0% 100%;
}
50% {
        background-position: 100% 0%;
}
100% {
        background-position: 0% 100%;
}
}

